<template>
  <div class="annotation-3d">
    <h2>3D</h2>
    
    <div class="content-container">
      <a-row :gutter="16">
        <a-col :span="16">
          <a-card title="3D" :bordered="true">
            <div class="model-actions">
              <a-button-group>
                <a-button type="primary" @click="loadDemoModel">
                  <a-icon type="experiment" /> 
                </a-button>
                <a-button :disabled="!modelLoaded" @click="resetCamera">
                  <a-icon type="reload" /> 
                </a-button>
              </a-button-group>
              
              <a-radio-group 
                v-model="currentTool" 
                button-style="solid" 
                style="margin-left: 16px"
                :disabled="!modelLoaded"
              >
                <a-radio-button value="navigate">
                  <a-icon type="global" /> 
                </a-radio-button>
                <a-radio-button value="addPoint">
                  <a-icon type="plus-circle" /> 
                </a-radio-button>
                <a-radio-button value="select" :disabled="annotations.length === 0">
                  <a-icon type="select" /> 
                </a-radio-button>
              </a-radio-group>
            </div>
            
            <div class="model-container" ref="container">
              <!-- Three.js  -->
              <div 
                v-if="!modelLoaded" 
                class="model-placeholder"
              >
                <a-empty description="3D">
                  <a-button type="primary" @click="loadDemoModel">
                    
                  </a-button>
                </a-empty>
              </div>
            </div>
          </a-card>
        </a-col>
        
        <a-col :span="8">
          <a-card title="" :bordered="true">
            <div v-if="modelLoaded">
              <div class="info-section">
                <h3></h3>
                <p>: {{ modelInfo.name || '' }}</p>
                <p>: {{ modelInfo.vertices || 0 }}</p>
                <p>: {{ annotations.length }}</p>
              </div>
              
              <a-divider />
              
              <div class="info-section">
                <h3></h3>
                <a-empty v-if="annotations.length === 0" description="" />
                <a-list v-else size="small" bordered>
                  <a-list-item 
                    v-for="(annotation, index) in annotations" 
                    :key="index"
                    :class="{ 'selected-item': selectedAnnotationIndex === index }"
                    @click="selectAnnotation(index)"
                  >
                    <a-list-item-meta>
                      <template slot="title">
                        <a-tag :color="annotation.color">{{ annotation.label || '' }}</a-tag>
                      </template>
                      <template slot="description">
                        <div class="anno-info">
                          : [{{ formatCoord(annotation.position.x) }}, 
                                {{ formatCoord(annotation.position.y) }}, 
                                {{ formatCoord(annotation.position.z) }}]
                        </div>
                      </template>
                    </a-list-item-meta>
                    <a-button 
                      type="danger" 
                      size="small" 
                      shape="circle"
                      @click.stop="removeAnnotation(index)"
                    >
                      <a-icon type="delete" />
                    </a-button>
                  </a-list-item>
                </a-list>
              </div>
              
              <a-divider />
              
              <div class="info-section" v-if="selectedAnnotationIndex !== null">
                <h3></h3>
                <a-form layout="vertical">
                  <a-form-item label="">
                    <a-input 
                      v-model="annotations[selectedAnnotationIndex].label"
                      placeholder=""
                      @change="updateAnnotation"
                    />
                  </a-form-item>
                  <a-form-item label="">
                    <a-select 
                      v-model="annotations[selectedAnnotationIndex].color" 
                      style="width: 100%"
                      @change="updateAnnotation"
                    >
                      <a-select-option value="blue"></a-select-option>
                      <a-select-option value="red"></a-select-option>
                      <a-select-option value="green"></a-select-option>
                      <a-select-option value="orange"></a-select-option>
                      <a-select-option value="purple"></a-select-option>
                    </a-select>
                  </a-form-item>
                  <a-form-item label="">
                    <a-textarea
                      v-model="annotations[selectedAnnotationIndex].description"
                      placeholder=""
                      :rows="3"
                      @change="updateAnnotation"
                    />
                  </a-form-item>
                </a-form>
              </div>
            </div>
            <a-empty v-else description="3D" />
          </a-card>
        </a-col>
      </a-row>
      
      <a-row style="margin-top: 16px">
        <a-col :span="24">
          <a-card title="" :bordered="true">
            <a-steps direction="vertical" size="small" :current="999">
              <a-step title="" description="3D" />
              <a-step title="" description="" />
              <a-step title="" description="" />
              <a-step title="" description="" />
              <a-step title="" description="" />
            </a-steps>
          </a-card>
        </a-col>
      </a-row>
    </div>
  </div>
</template>

<script>
import * as THREE from 'three';
import { OrbitControls } from 'three/examples/jsm/controls/OrbitControls';
import { GLTFLoader } from 'three/examples/jsm/loaders/GLTFLoader';

export default {
  name: 'Annotation3D',
  data() {
    return {
      scene: null,
      camera: null,
      renderer: null,
      controls: null,
      model: null,
      raycaster: null,
      mouse: null,
      
      modelLoaded: false,
      modelInfo: {
        name: '',
        vertices: 0
      },
      
      currentTool: 'navigate',
      annotations: [],
      selectedAnnotationIndex: null,
      
      animationId: null
    }
  },
  mounted() {
    window.addEventListener('resize', this.handleResize);
  },
  beforeDestroy() {
    this.cleanup();
    window.removeEventListener('resize', this.handleResize);
  },
  methods: {
    // Three.js 
    initThree() {
      const container = this.$refs.container;
      
      // 
      this.scene = new THREE.Scene();
      this.scene.background = new THREE.Color(0xf0f0f0);
      
      // 
      const ambientLight = new THREE.AmbientLight(0x404040, 1);
      this.scene.add(ambientLight);
      
      const directionalLight = new THREE.DirectionalLight(0xffffff, 0.8);
      directionalLight.position.set(1, 1, 1).normalize();
      this.scene.add(directionalLight);
      
      // 
      this.camera = new THREE.PerspectiveCamera(
        75, 
        container.clientWidth / container.clientHeight, 
        0.1, 
        1000
      );
      this.camera.position.z = 5;
      
      // 
      this.renderer = new THREE.WebGLRenderer({ antialias: true });
      this.renderer.setSize(container.clientWidth, container.clientHeight);
      container.appendChild(this.renderer.domElement);
      
      // 
      this.controls = new OrbitControls(this.camera, this.renderer.domElement);
      this.controls.enableDamping = true;
      this.controls.dampingFactor = 0.25;
      this.controls.screenSpacePanning = false;
      this.controls.maxPolarAngle = Math.PI / 2;
      
      // 
      this.raycaster = new THREE.Raycaster();
      this.mouse = new THREE.Vector2();
      
      // 
      const axesHelper = new THREE.AxesHelper(5);
      this.scene.add(axesHelper);
      
      // 
      const gridHelper = new THREE.GridHelper(10, 10);
      this.scene.add(gridHelper);
      
      // 
      this.renderer.domElement.addEventListener('click', this.handleClick);
      
      // 
      this.animate();
    },
    
    animate() {
      this.animationId = requestAnimationFrame(this.animate);
      
      if (this.controls) {
        this.controls.update();
      }
      
      this.renderer.render(this.scene, this.camera);
    },
    
    loadDemoModel() {
      if (!this.scene) {
        this.initThree();
      }
      
      // 
      if (this.model) {
        this.scene.remove(this.model);
        this.model = null;
      }
      
      // 
      const geometry = new THREE.BoxGeometry(2, 2, 2);
      const material = new THREE.MeshStandardMaterial({ 
        color: 0x3080ff,
        metalness: 0.2,
        roughness: 0.5
      });
      
      this.model = new THREE.Mesh(geometry, material);
      this.scene.add(this.model);
      
      // 
      this.modelInfo = {
        name: '',
        vertices: geometry.attributes.position.count
      };
      
      this.modelLoaded = true;
      this.resetCamera();
    },
    
    handleClick(event) {
      if (!this.modelLoaded || this.currentTool === 'navigate') return;
      
      // 
      const rect = this.renderer.domElement.getBoundingClientRect();
      this.mouse.x = ((event.clientX - rect.left) / rect.width) * 2 - 1;
      this.mouse.y = -((event.clientY - rect.top) / rect.height) * 2 + 1;
      
      // 
      this.raycaster.setFromCamera(this.mouse, this.camera);
      
      if (this.currentTool === 'addPoint') {
        // 
        const intersects = this.raycaster.intersectObject(this.model);
        
        if (intersects.length > 0) {
          this.addAnnotationPoint(intersects[0].point);
        }
      } else if (this.currentTool === 'select') {
        // 
        const annotationObjects = this.annotations.map(anno => anno.marker);
        
        // 
        const intersects = this.raycaster.intersectObjects(annotationObjects);
        
        if (intersects.length > 0) {
          // 
          const markerIndex = annotationObjects.indexOf(intersects[0].object);
          if (markerIndex !== -1) {
            this.selectAnnotation(markerIndex);
          }
        } else {
          this.selectedAnnotationIndex = null;
        }
      }
    },
    
    addAnnotationPoint(position) {
      // 
      const geometry = new THREE.SphereGeometry(0.1, 16, 16);
      const material = new THREE.MeshBasicMaterial({ color: 0x0066ff });
      const marker = new THREE.Mesh(geometry, material);
      
      // 
      marker.position.copy(position);
      
      // 
      this.scene.add(marker);
      
      // 
      const annotation = {
        position: position.clone(),
        label: ` ${this.annotations.length + 1}`,
        color: 'blue',
        description: '',
        marker: marker
      };
      
      // 
      this.annotations.push(annotation);
      this.selectedAnnotationIndex = this.annotations.length - 1;
    },
    
    selectAnnotation(index) {
      this.selectedAnnotationIndex = index;
      
      // 
      this.annotations.forEach((anno, i) => {
        if (i === index) {
          anno.marker.material.color.set(this.getColorHex(anno.color));
          anno.marker.material.opacity = 1;
          anno.marker.scale.set(1.5, 1.5, 1.5);
        } else {
          anno.marker.material.color.set(this.getColorHex(anno.color));
          anno.marker.material.opacity = 0.7;
          anno.marker.scale.set(1, 1, 1);
        }
      });
    },
    
    removeAnnotation(index) {
      // 
      this.scene.remove(this.annotations[index].marker);
      
      // 
      this.annotations.splice(index, 1);
      
      // 
      if (this.selectedAnnotationIndex === index) {
        this.selectedAnnotationIndex = null;
      } else if (this.selectedAnnotationIndex > index) {
        this.selectedAnnotationIndex--;
      }
    },
    
    updateAnnotation() {
      if (this.selectedAnnotationIndex === null) return;
      
      const annotation = this.annotations[this.selectedAnnotationIndex];
      
      // 
      annotation.marker.material.color.set(this.getColorHex(annotation.color));
    },
    
    resetCamera() {
      if (!this.camera || !this.controls) return;
      
      this.camera.position.set(0, 3, 5);
      this.camera.lookAt(0, 0, 0);
      this.controls.target.set(0, 0, 0);
      this.controls.update();
    },
    
    handleResize() {
      if (!this.camera || !this.renderer || !this.$refs.container) return;
      
      const container = this.$refs.container;
      
      this.camera.aspect = container.clientWidth / container.clientHeight;
      this.camera.updateProjectionMatrix();
      
      this.renderer.setSize(container.clientWidth, container.clientHeight);
    },
    
    cleanup() {
      if (this.animationId) {
        cancelAnimationFrame(this.animationId);
      }
      
      if (this.renderer) {
        this.renderer.domElement.removeEventListener('click', this.handleClick);
        this.$refs.container.removeChild(this.renderer.domElement);
        this.renderer.dispose();
      }
      
      // 
      if (this.scene) {
        this.scene.clear();
      }
      
      this.scene = null;
      this.camera = null;
      this.renderer = null;
      this.controls = null;
      this.model = null;
    },
    
    formatCoord(value) {
      return value.toFixed(2);
    },
    
    getColorHex(colorName) {
      const colorMap = {
        'blue': 0x1890ff,
        'red': 0xff4d4f,
        'green': 0x52c41a,
        'orange': 0xfa8c16,
        'purple': 0x722ed1
      };
      
      return colorMap[colorName] || 0x1890ff;
    }
  }
}
</script>

<style scoped>
.annotation-3d {
  padding: 20px;
}

.content-container {
  margin-top: 20px;
}

.model-actions {
  margin-bottom: 16px;
  display: flex;
  align-items: center;
}

.model-container {
  position: relative;
  height: 500px;
  background-color: #f0f0f0;
  border: 1px solid #eee;
  overflow: hidden;
}

.model-placeholder {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #fafafa;
}

.info-section {
  margin-bottom: 16px;
}

.info-section h3 {
  margin-bottom: 8px;
  font-weight: 500;
}

.anno-info {
  font-size: 12px;
  color: #666;
  margin-bottom: 2px;
}

.selected-item {
  background-color: #e6f7ff;
}
</style> 