<template>
  <div class="audio-annotation">
    <h2></h2>
    
    <div class="content-container">
      <a-row :gutter="16">
        <a-col :span="24">
          <a-card title="" :bordered="true">
            <div class="audio-actions">
              <a-upload
                name="file"
                :show-upload-list="false"
                :before-upload="beforeAudioUpload"
                @change="handleAudioChange"
              >
                <a-button :disabled="audioLoading">
                  <a-icon type="upload" /> 
                </a-button>
              </a-upload>
              
              <div class="play-controls" v-if="audioLoaded">
                <a-button-group>
                  <a-button @click="playAudio">
                    <a-icon :type="isPlaying ? 'pause-circle' : 'play-circle'" />
                    {{ isPlaying ? '' : '' }}
                  </a-button>
                  <a-button @click="stopAudio">
                    <a-icon type="stop" /> 
                  </a-button>
                </a-button-group>
                
                <a-radio-group 
                  v-model="currentTool" 
                  button-style="solid" 
                  style="margin-left: 16px"
                >
                  <a-radio-button value="navigate">
                    <a-icon type="drag" /> 
                  </a-radio-button>
                  <a-radio-button value="addMarker">
                    <a-icon type="plus-circle" /> 
                  </a-radio-button>
                  <a-radio-button value="addRegion">
                    <a-icon type="scissor" /> 
                  </a-radio-button>
                </a-radio-group>
                
                <span class="time-display" v-if="audioLoaded">
                  {{ formatTime(currentTime) }} / {{ formatTime(duration) }}
                </span>
              </div>
            </div>
            
            <div class="audio-container">
              <div class="waveform-container" ref="waveformContainer">
                <!-- wavesurfer.js  -->
              </div>
              
              <div class="markers-container" ref="markersContainer">
                <!--  -->
                <div 
                  v-for="(marker, index) in markers" 
                  :key="'marker-'+index"
                  class="marker"
                  :class="{ 'selected': selectedMarkerIndex === index }"
                  :style="{ left: `${marker.position * 100}%` }"
                  @click.stop="selectMarker(index)"
                >
                  <div class="marker-line"></div>
                  <div class="marker-label">{{ marker.label }}</div>
                </div>
              </div>
              
              <div v-if="!audioLoaded" class="upload-placeholder">
                <a-empty description="">
                  <a-upload
                    name="file"
                    :show-upload-list="false"
                    :before-upload="beforeAudioUpload"
                    @change="handleAudioChange"
                  >
                    <a-button type="primary">
                      <a-icon type="upload" /> 
                    </a-button>
                  </a-upload>
                </a-empty>
              </div>
            </div>
          </a-card>
        </a-col>
      </a-row>
      
      <!--  -->
      <a-row :gutter="16" style="margin-top: 16px">
        <a-col :span="24">
          <a-card title="" :bordered="true">
            <a-empty v-if="regions.length === 0" description="">
              <template #description>
                <p>""</p>
              </template>
            </a-empty>
            <a-table
              v-else
              :columns="regionColumns"
              :data-source="regionTableData"
              :pagination="false"
              :row-class-name="getRegionRowClassName"
              @row-click="onRegionRowClick"
            >
              <template slot="action" slot-scope="text, record, index">
                <a-button 
                  type="link" 
                  size="small" 
                  @click.stop="() => removeRegion(record.id)" 
                  style="color: #ff4d4f"
                ></a-button>
                <a-button 
                  type="link" 
                  size="small" 
                  @click.stop="() => playRegion(record.id)"
                ></a-button>
              </template>
              <template slot="time" slot-scope="text, record">
                {{ formatTime(record.start) }} - {{ formatTime(record.end) }}
              </template>
            </a-table>
          </a-card>
        </a-col>
      </a-row>
      
      <!--  -->
      <a-row :gutter="16" style="margin-top: 16px">
        <a-col :span="12">
          <a-card title="" :bordered="true">
            <a-empty v-if="markers.length === 0" description="" />
            <a-table
              v-else
              :columns="markerColumns"
              :data-source="markerTableData"
              :pagination="false"
              :row-class-name="getRowClassName"
              @row-click="onRowClick"
            >
              <template slot="action" slot-scope="text, record, index">
                <a-button 
                  type="link" 
                  size="small" 
                  @click.stop="() => removeMarker(index)" 
                  style="color: #ff4d4f"
                ></a-button>
                <a-button 
                  type="link" 
                  size="small" 
                  @click.stop="() => seekToMarker(index)"
                ></a-button>
              </template>
            </a-table>
          </a-card>
        </a-col>
        
        <a-col :span="12">
          <!--  -->
          <a-card :title="detailTitle" :bordered="true">
            <!--  -->
            <div v-if="selectedType === 'marker' && selectedMarkerIndex !== null">
              <a-form layout="vertical">
                <a-form-item label="">
                  <a-input-number
                    v-model="markers[selectedMarkerIndex].time"
                    :min="0"
                    :max="duration"
                    :precision="2"
                    style="width: 100%"
                    @change="updateMarkerTime"
                  />
                </a-form-item>
                <a-form-item label="">
                  <a-input 
                    v-model="markers[selectedMarkerIndex].label" 
                    placeholder=""
                  />
                </a-form-item>
                <a-form-item label="">
                  <a-select 
                    v-model="markers[selectedMarkerIndex].type" 
                    style="width: 100%"
                  >
                    <a-select-option value="speech"></a-select-option>
                    <a-select-option value="music"></a-select-option>
                    <a-select-option value="noise"></a-select-option>
                    <a-select-option value="other"></a-select-option>
                  </a-select>
                </a-form-item>
                <a-form-item label="">
                  <a-textarea
                    v-model="markers[selectedMarkerIndex].description"
                    placeholder=""
                    :rows="3"
                  />
                </a-form-item>
              </a-form>
            </div>
            
            <!--  -->
            <div v-else-if="selectedType === 'region' && selectedRegion">
              <a-form layout="vertical">
                <a-form-item label="">
                  <a-input-number
                    v-model="selectedRegion.start"
                    :min="0"
                    :max="selectedRegion.end"
                    :precision="2"
                    style="width: 100%"
                    @change="updateRegionBoundary"
                  />
                </a-form-item>
                <a-form-item label="">
                  <a-input-number
                    v-model="selectedRegion.end"
                    :min="selectedRegion.start"
                    :max="duration"
                    :precision="2"
                    style="width: 100%"
                    @change="updateRegionBoundary"
                  />
                </a-form-item>
                <a-form-item label="">
                  <a-input 
                    v-model="selectedRegion.data.label" 
                    placeholder=""
                    @change="updateRegionData"
                  />
                </a-form-item>
                <a-form-item label="">
                  <a-textarea
                    v-model="selectedRegion.data.content"
                    placeholder=""
                    :rows="5"
                    @change="updateRegionData"
                  />
                </a-form-item>
              </a-form>
            </div>
            
            <!--  -->
            <a-empty v-else description="" />
          </a-card>
        </a-col>
      </a-row>
      
      <a-row style="margin-top: 16px">
        <a-col :span="24">
          <a-card title="" :bordered="true">
            <a-steps direction="vertical" size="small" :current="999">
              <a-step title="" description="" />
              <a-step title="" description="" />
              <a-step title="" description="" />
              <a-step title="" description="" />
              <a-step title="" description="" />
            </a-steps>
          </a-card>
        </a-col>
      </a-row>
    </div>
  </div>
</template>

<script>
import WaveSurfer from 'wavesurfer.js';
import RegionsPlugin from 'wavesurfer.js/dist/plugin/wavesurfer.regions.min.js';

export default {
  name: 'AudioAnnotation',
  data() {
    return {
      wavesurfer: null,
      audioLoaded: false,
      audioLoading: false,
      isPlaying: false,
      duration: 0,
      currentTime: 0,
      
      // 
      currentTool: 'navigate',
      
      // 
      markers: [],
      selectedMarkerIndex: null,
      
      // 
      regions: [],
      selectedRegionId: null,
      
      // markerregion
      selectedType: null,
      
      // 
      markerColumns: [
        {
          title: '',
          dataIndex: 'time',
          key: 'time',
          sorter: (a, b) => a.rawTime - b.rawTime
        },
        {
          title: '',
          dataIndex: 'label',
          key: 'label'
        },
        {
          title: '',
          dataIndex: 'type',
          key: 'type'
        },
        {
          title: '',
          key: 'action',
          scopedSlots: { customRender: 'action' }
        }
      ],
      
      // 
      regionColumns: [
        {
          title: '',
          key: 'time',
          scopedSlots: { customRender: 'time' }
        },
        {
          title: '',
          dataIndex: 'label',
          key: 'label'
        },
        {
          title: '',
          dataIndex: 'content',
          key: 'content',
          ellipsis: true
        },
        {
          title: '',
          key: 'action',
          scopedSlots: { customRender: 'action' }
        }
      ],
      
      // 
      demoAudioBase64: 'data:audio/wav;base64,UklGRiQAAABXQVZFZm10IBAAAAABAAEARKwAAIhYAQACABAAZGF0YQAAAAA=',
      regionsPlugin: null,
    }
  },
  computed: {
    // 
    markerTableData() {
      return this.markers.map((marker, index) => ({
        key: index,
        time: this.formatTime(marker.time),
        label: marker.label,
        type: this.getTypeLabel(marker.type),
        rawTime: marker.time
      }));
    },
    
    // 
    regionTableData() {
      return this.regions.map(region => ({
        key: region.id,
        id: region.id,
        start: region.start,
        end: region.end,
        label: region.data.label || '',
        content: region.data.content ? 
          (region.data.content.length > 30 ? 
            region.data.content.substring(0, 30) + '...' : 
            region.data.content) : 
          ''
      }));
    },
    
    // 
    selectedRegion() {
      if (!this.selectedRegionId) return null;
      return this.regions.find(r => r.id === this.selectedRegionId);
    },
    
    // 
    detailTitle() {
      if (this.selectedType === 'marker') {
        return '';
      } else if (this.selectedType === 'region') {
        return '';
      }
      return '';
    }
  },
  mounted() {
    // WaveSurfer
    this.initAudio();
  },
  beforeDestroy() {
    // WaveSurfer
    if (this.wavesurfer) {
      this.wavesurfer.destroy();
    }
  },
  methods: {
    initAudio() {
      // 
      if (this.wavesurfer) {
        this.wavesurfer.destroy();
      }
      
      //  WaveSurfer 
      this.wavesurfer = WaveSurfer.create({
        container: this.$refs.waveformContainer,
        waveColor: '#3498db',
        progressColor: '#1890ff',
        cursorColor: '#f5222d',
        height: 150,
        barWidth: 2,
        barGap: 1,
        responsive: true,
        normalize: true,
        plugins: [
          RegionsPlugin.create({
            dragSelection: {
              slop: 5
            },
            regions: []
          })
        ]
      });
      
      // 
      this.wavesurfer.on('ready', () => {
        this.audioLoaded = true;
        this.duration = this.wavesurfer.getDuration();
      });
      
      this.wavesurfer.on('play', () => {
        this.isPlaying = true;
      });
      
      this.wavesurfer.on('pause', () => {
        this.isPlaying = false;
      });
      
      this.wavesurfer.on('audioprocess', () => {
        this.currentTime = this.wavesurfer.getCurrentTime();
      });
      
      this.wavesurfer.on('seeking', () => {
        this.currentTime = this.wavesurfer.getCurrentTime();
      });
      
      this.wavesurfer.on('click', (e) => {
        this.handleWaveClick(e);
      });
      
      // 
      this.wavesurfer.on('region-created', region => {
        this.handleRegionCreated(region);
      });
      
      // 
      this.wavesurfer.on('region-click', (region, e) => {
        e.stopPropagation();
        this.selectRegion(region.id);
      });
      
      this.wavesurfer.on('region-update-end', (region) => {
        // 
        const existingRegion = this.regions.find(r => r.id === region.id);
        if (existingRegion) {
          existingRegion.start = region.start;
          existingRegion.end = region.end;
        }
      });
      
      // 
      const blob = this.dataURItoBlob(this.demoAudioBase64);
      const audioUrl = URL.createObjectURL(blob);
      this.wavesurfer.load(audioUrl);
      this.audioLoading = true;
    },
    
    // updateDragSelection
    updateDragSelection() {
      // wavesurferregions
      if (!this.wavesurfer || !this.wavesurfer.regions) return;
      
      // 
      const enableDragSelection = this.currentTool === 'addRegion';
      
      try {
        // /
        if (enableDragSelection) {
          this.wavesurfer.regions.enableDragSelection({
            color: 'rgba(0, 128, 255, 0.1)'
          });
        } else {
          this.wavesurfer.regions.disableDragSelection();
        }
      } catch (error) {
        console.error(':', error);
      }
    },
    
    // 
    handleRegionCreated(region) {
      // 
      if (this.currentTool !== 'addRegion') {
        region.remove();
        return;
      }
      
      // 
      region.label = ` ${this.regions.length + 1}`;
      region.content = '';
      region.type = 'speech';
      
      // 
      this.regions.push(region);
      
      // 
      this.selectRegion(region.id);
    },
    
    // 
    initRegions() {
      this.regions = [];
      this.selectedRegionId = null;
    },
    
    beforeAudioUpload(file) {
      const isAudio = file.type.startsWith('audio/');
      if (!isAudio) {
        this.$message.error('!');
        return false;
      }
      const isLt10M = file.size / 1024 / 1024 < 10;
      if (!isLt10M) {
        this.$message.error('10MB!');
        return false;
      }
      return false; // 
    },
    
    handleAudioChange(info) {
      if (info.file) {
        this.audioLoading = true;
        
        // 
        this.markers = [];
        this.selectedMarkerIndex = null;
        
        // 
        this.clearRegions();
        this.selectedRegionId = null;
        this.selectedType = null;
        
        // WaveSurfer
        const reader = new FileReader();
        reader.onload = (e) => {
          this.wavesurfer.loadArrayBuffer(e.target.result);
          this.audioLoading = false;
        };
        reader.readAsArrayBuffer(info.file);
      }
    },
    
    playAudio() {
      if (this.isPlaying) {
        this.wavesurfer.pause();
      } else {
        this.wavesurfer.play();
      }
    },
    
    stopAudio() {
      this.wavesurfer.stop();
      this.isPlaying = false;
    },
    
    // 
    playRegion(regionId) {
      const region = this.regions.find(r => r.id === regionId);
      if (region) {
        this.selectRegion(regionId);
        region.play();
      }
    },
    
    handleWaveClick(e) {
      if (this.currentTool !== 'addMarker') return;
      
      const position = e.offsetX / this.$refs.waveformContainer.offsetWidth;
      const time = this.wavesurfer.getDuration() * position;
      this.addMarker(time, position);
    },
    
    addMarker(time, position) {
      const marker = {
        time: time,
        position: position,
        label: ` ${this.markers.length + 1}`,
        type: 'speech',
        description: ''
      };
      
      this.markers.push(marker);
      this.selectedMarkerIndex = this.markers.length - 1;
      this.selectedType = 'marker';
      
      // 
      this.sortMarkers();
    },
    
    selectMarker(index) {
      this.selectedMarkerIndex = index;
      this.selectedType = 'marker';
      this.selectedRegionId = null; // 
    },
    
    selectRegion(regionId) {
      this.selectedRegionId = regionId;
      this.selectedType = 'region';
      this.selectedMarkerIndex = null; // 
      
      // 
      this.regions.forEach(region => {
        if (region.id === regionId) {
          region.setOptions({ color: 'rgba(255, 0, 0, 0.2)' });
        } else {
          region.setOptions({ color: 'rgba(0, 0, 255, 0.1)' });
        }
      });
    },
    
    removeMarker(index) {
      this.$confirm({
        title: '?',
        content: '',
        okText: '',
        okType: 'danger',
        cancelText: '',
        onOk: () => {
          this.markers.splice(index, 1);
          if (this.selectedMarkerIndex === index) {
            this.selectedMarkerIndex = null;
            this.selectedType = null;
          } else if (this.selectedMarkerIndex > index) {
            this.selectedMarkerIndex--;
          }
        }
      });
    },
    
    removeRegion(regionId) {
      this.$confirm({
        title: '?',
        content: '',
        okText: '',
        okType: 'danger',
        cancelText: '',
        onOk: () => {
          // 
          const regionIndex = this.regions.findIndex(r => r.id === regionId);
          if (regionIndex >= 0) {
            const region = this.regions[regionIndex];
            region.remove();  // 
            this.regions.splice(regionIndex, 1);  // 
            
            // 
            if (this.selectedRegionId === regionId) {
              this.selectedRegionId = null;
              this.selectedType = null;
            }
          }
        }
      });
    },
    
    // 
    clearRegions() {
      this.regions.forEach(region => {
        region.remove();
      });
      this.regions = [];
    },
    
    updateMarkerTime() {
      if (this.selectedMarkerIndex === null) return;
      
      const marker = this.markers[this.selectedMarkerIndex];
      marker.position = marker.time / this.duration;
      
      // 
      this.sortMarkers();
    },
    
    // 
    updateRegionBoundary() {
      if (!this.selectedRegion) return;
      
      // wavesurfer
      this.selectedRegion.update({
        start: this.selectedRegion.start,
        end: this.selectedRegion.end
      });
    },
    
    // 
    updateRegionData() {
      // 
    },
    
    sortMarkers() {
      // 
      this.markers.sort((a, b) => a.time - b.time);
      
      // 
      if (this.selectedMarkerIndex !== null) {
        const selectedMarker = this.markers[this.selectedMarkerIndex];
        const newIndex = this.markers.findIndex(m => m.time === selectedMarker.time);
        this.selectedMarkerIndex = newIndex;
      }
    },
    
    seekToMarker(index) {
      if (index < 0 || index >= this.markers.length) return;
      
      const time = this.markers[index].time;
      this.wavesurfer.seekTo(time / this.duration);
      this.selectMarker(index);
    },
    
    onRowClick(record) {
      this.selectMarker(record.key);
    },
    
    onRegionRowClick(record) {
      this.selectRegion(record.id);
      
      // 
      const region = this.regions.find(r => r.id === record.id);
      if (region) {
        this.wavesurfer.seekTo(region.start / this.duration);
      }
    },
    
    getRowClassName(record) {
      return record.key === this.selectedMarkerIndex && this.selectedType === 'marker' ? 'selected-row' : '';
    },
    
    getRegionRowClassName(record) {
      return record.id === this.selectedRegionId && this.selectedType === 'region' ? 'selected-row' : '';
    },
    
    formatTime(time) {
      if (!time && time !== 0) return '00:00.00';
      
      const minutes = Math.floor(time / 60);
      const seconds = Math.floor(time % 60);
      const milliseconds = Math.floor((time % 1) * 100);
      
      return `${minutes.toString().padStart(2, '0')}:${seconds.toString().padStart(2, '0')}.${milliseconds.toString().padStart(2, '0')}`;
    },
    
    getTypeLabel(type) {
      const typeMap = {
        'speech': '',
        'music': '',
        'noise': '',
        'other': ''
      };
      
      return typeMap[type] || '';
    },
    
    dataURItoBlob(dataURI) {
      const byteString = atob(dataURI.split(',')[1]);
      const mimeString = dataURI.split(',')[0].split(':')[1].split(';')[0];
      const ab = new ArrayBuffer(byteString.length);
      const ia = new Uint8Array(ab);
      
      for (let i = 0; i < byteString.length; i++) {
        ia[i] = byteString.charCodeAt(i);
      }
      
      return new Blob([ab], { type: mimeString });
    }
  },
  watch: {
    // 
    currentTool() {
      this.updateDragSelection();
    }
  }
}
</script>

<style scoped>
.audio-annotation {
  padding: 20px;
}

.content-container {
  margin-top: 20px;
}

.audio-actions {
  margin-bottom: 16px;
  display: flex;
  align-items: center;
  flex-wrap: wrap;
}

.play-controls {
  display: flex;
  align-items: center;
  margin-left: 16px;
}

.time-display {
  margin-left: 16px;
  font-family: monospace;
  font-size: 14px;
  color: #555;
}

.audio-container {
  position: relative;
  height: 150px;
  background-color: #f9f9f9;
  border: 1px solid #eee;
  overflow: hidden;
}

.waveform-container {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 1;
}

.markers-container {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 2;
  pointer-events: none;
}

.marker {
  position: absolute;
  top: 0;
  height: 100%;
  width: 2px;
  pointer-events: all;
  cursor: pointer;
}

.marker-line {
  position: absolute;
  top: 0;
  left: 0;
  width: 2px;
  height: 100%;
  background-color: #ff5500;
}

.marker-label {
  position: absolute;
  top: 5px;
  left: 5px;
  padding: 2px 4px;
  background-color: rgba(255, 85, 0, 0.8);
  color: white;
  font-size: 12px;
  border-radius: 2px;
  white-space: nowrap;
}

.marker.selected .marker-line {
  background-color: #ff0000;
  width: 3px;
}

.marker.selected .marker-label {
  background-color: #ff0000;
  font-weight: bold;
}

.upload-placeholder {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #fafafa;
  z-index: 3;
}

.selected-row {
  background-color: #e6f7ff;
}

/*  */
@media (max-width: 768px) {
  .play-controls {
    margin-left: 0;
    margin-top: 10px;
  }
  
  .time-display {
    margin-left: auto;
  }
}
</style> 